<template>
  <view class="content">
    <button @click="getRegeo">获取位置信息</button>
  </view>
</template>

<script>
  import amap from "../../commons/amap-wx.130.js"
  export default {
    data() {
      return {
        amapPlugin: null,
        key: '654af345ebef3b6de4e40dfca7c5d617'
      }
    },
    onLoad() {
      this.amapPlugin = new amap.AMapWX({
        key: this.key
      });
    },
    methods: {
      getRegeo() {
        uni.showLoading({
          title: '获取信息中'
        });
        this.amapPlugin.getRegeo({
          success: (data) => {
            console.log(data)
            this.addressName = data[0].name;
            uni.hideLoading();
          },
          fail(e) {
            uni.hideLoading();
            console.log(e)
          }
        });
      }
    }
  }
</script>

<style>
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .logo {
    height: 200rpx;
    width: 200rpx;
    margin-top: 200rpx;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50rpx;
  }

  .text-area {
    display: flex;
    justify-content: center;
  }

  .title {
    font-size: 36rpx;
    color: #8f8f94;
  }
</style>
